import Vue from "vue";
import Router from "vue-router";
import NProgress from "nprogress";
import axios from "axios";
import NotFound from "./views/NotFound";

import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";
Vue.use(Router);

const router = new Router({
  // mode: "history",
  // base: "/events/05-2019/ticketing/",
  routes: [
    {
      path: "/:eventSlug/rsvp/:personId",
      name: "rsvp",
      component: () => import(/* webpackChunkName: "rsvp" */ "./views/RSVP"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            ProfileService.getProfile(routeTo.params.personId),
            TicketService.getEvent(routeTo.params.eventSlug)
          ])
          .then(
            axios.spread((personRes, eventRes) => {
              const event = eventRes.data.event;
              const person = personRes.data.person;
              routeTo.params.event = event;
              routeTo.params.person = person;
              if (person.eventRSVPIds.includes(event.id)) {
                TicketService.getRSVP(event.slug, person.id).then(res => {
                  routeTo.params.rsvp = res.data.rsvp;
                  next();
                });
              } else {
                TicketService.getNewRSVP(event.slug).then(res => {
                  routeTo.params.rsvp = res.data.rsvp;
                  next();
                });
              }
            })
          )
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/rsvp",
      name: "rsvp-new-person",
      component: () => import(/* webpackChunkName: "rsvp" */ "./views/RSVP"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            ProfileService.getNewProfile(),
            TicketService.getEvent(routeTo.params.eventSlug)
          ])
          .then(
            axios.spread((personRes, eventRes) => {
              const event = eventRes.data.event;
              const person = personRes.data.person;
              routeTo.params.event = event;
              routeTo.params.person = person;
              TicketService.getNewRSVP(event.slug).then(res => {
                routeTo.params.rsvp = res.data.rsvp;
                next();
              });
            })
          )
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply",
      name: "apply",
      component: () => import(/* webpackChunkName: "apply" */ "./views/Apply"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getEvent(routeTo.params.eventSlug)
          .then(response => {
            routeTo.params.event = response.data.event;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/:id",
      name: "application-status",
      component: () =>
        import(/* webpackChunkName: "application-status" */ "./views/ApplicationStatus"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getApplication(
          routeTo.params.eventSlug,
          routeTo.params.id
        )
          .then(response => {
            const application = response.data.application;
            const event = response.data.event;
            const ticketOffer = response.data.ticketOffer;
            if (application.status === "pending") {
              routeTo.params.applicant = response.data.applicant;
              routeTo.params.application = application;
              routeTo.params.ticketOffer = ticketOffer;
              routeTo.params.event = event;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: {
                  eventSlug: routeTo.params.eventSlug,
                  id: ticketOffer.id
                }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/update-ticket-info/:id",
      name: "update-ticket-info",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "./views/TicketDetails"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(
          routeTo.params.eventSlug,
          routeTo.params.id
        )
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            const event = response.data.event;
            routeTo.params.personInit = response.data.recipient;
            routeTo.params.ticketOffer = ticketOffer;
            routeTo.params.event = event;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/offer/spaces-full",
      name: "spaces-full",
      component: () =>
        import(/* webpackChunkName: "spaces-full" */ "./views/SpacesFull"),
      props: true
    },
    {
      path: "/:eventSlug/apply/offer/:id",
      name: "ticket-offer-from-application",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "./views/TicketOfferFromApplication"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(
          routeTo.params.eventSlug,
          routeTo.params.id
        )
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            const event = response.data.event;
            const person = response.data.recipient;
            if (event.soldOut) {
              next({
                name: "spaces-full",
                params: {
                  personName: person.firstName,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else if (ticketOffer.status === "void") {
              next({
                name: "ticket-offer-from-application",
                params: {
                  id: ticketOffer.newTicketOfferId,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else if (ticketOffer.status === "purchased") {
              next({
                name: "ticket-offer-purchased",
                params: {
                  id: routeTo.params.id,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else if (ticketOffer.status === "surrendered") {
              next({
                name: "ticket-offer-surrendered",
                params: {
                  id: routeTo.params.id,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else if (ticketOffer.status === "expiration_passed") {
              next({
                name: "ticket-offer-expired",
                params: {
                  id: routeTo.params.id,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else {
              routeTo.params.personInit = response.data.recipient;
              routeTo.params.ticketOffer = ticketOffer;
              routeTo.params.event = event;
              next();
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id",
      name: "ticket-offer-partner",
      component: () =>
        import(/* webpackChunkName: "partner-ticket-offer" */ "./views/TicketOfferPartner"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;

            if (ticketOffer.status === "purchased") {
              next({
                name: "partner-ticket-offer-purchased",
                params: { id: routeTo.params.id }
              });
            } else if (ticketOffer.status === "surrendered") {
              next({
                name: "partner-ticket-offer-surrendered",
                params: { id: routeTo.params.id }
              });
            } else if (ticketOffer.status === "expiration_passed") {
              next({
                name: "partner-ticket-offer-expired",
                params: { id: routeTo.params.id }
              });
            } else {
              routeTo.params.personInit = response.data.recipient;
              routeTo.params.ticketOffer = ticketOffer;
              routeTo.params.event = response.data.event;
              next();
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id/purchased",
      name: "partner-ticket-offer-purchased",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-purchased" */ "./views/TicketOfferPurchased"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "purchased") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-partner",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id/surrendered",
      name: "partner-ticket-offer-surrendered",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-surrendered" */ "./views/TicketOfferSurrendered"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "surrendered") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/ticket-offer/:id/expired",
      name: "partner-ticket-offer-expired",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-expired" */ "./views/TicketOfferExpired"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "expiration_passed") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/:id/purchased",
      name: "ticket-offer-purchased",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-purchased" */ "./views/TicketOfferPurchased"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "purchased") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/:id/surrendered",
      name: "ticket-offer-surrendered",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-surrendered" */ "./views/TicketOfferSurrendered"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "surrendered") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/:id/expired",
      name: "ticket-offer-expired",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-surrendered" */ "./views/TicketOfferSurrendered"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "expiration_passed") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/404",
      name: "404",
      component: NotFound
    },
    {
      path: "*",
      redirect: { name: "404" }
    }
  ]
});

router.beforeEach((routeTo, routeFrom, next) => {
  NProgress.start();
  next();
});

router.afterEach(() => {
  NProgress.done();
});

export default router;
